import { Button, TextField } from "@mui/material";
import React, { useState } from "react";
import axios from "axios";

const ProfilPage = ({ user, setUser }) => {
  const [username, setUsername] = useState();

  const ChangeYourPassword = async () => {
    let formField = new FormData();
    //same names as django database fields
    formField.append("email", user.email);
    try {
      await axios({
        method: "post",
        url: `https://cityty-server-fe9e2e38c772.herokuapp.com/api/forgotpassword/`,
        data: formField,
      });
      alert(`Un lien de rinitialisation a t envoy  ${user.email}`);
    } catch {
      alert("Erreur de connexion, le lien de rinitialisation n'a pas t envoy...");
    }
  };

  const ChangeUsername = async () => {
    let formField = new FormData();
    //same names as django database fields
    formField.append("username", username);
    const token = localStorage.getItem("token");
    try {
      await axios({
        method: "post",
        url: `https://cityty-server-fe9e2e38c772.herokuapp.com/api/change_username/`,
        data: formField,
        headers: {
          Authorization: `Token ${token}`,
        },
      }).then((response) => {
        alert(response.data);
        fetchData();
      });
    } catch {
      alert("Ce nom d'utilisateur ne convient pas ou est dj utilis...");
    }
  };

  const fetchData = async () => {
    const token = localStorage.getItem("token");
    try {
      const response = await axios.get(
        "https://cityty-server-fe9e2e38c772.herokuapp.com/api/user_profil/",
        {
          headers: {
            Authorization: `Token ${token}`,
          },
        }
      );
      console.log(user)
      setUser(response.data);
    } catch (error) {
      console.error(
        "Erreur lors de la rcupration des donnes de l'utilisateur :",
        error
      );
    }
  };

  return (
    <div className="profilPageBox">
      <text className="textProfil1">Email : {user.email}</text>
      <text className="textProfil1">Username : {user.username}</text>
      <div className="boxChangePassword">
        <TextField
          id="password"
          label="Change your username"
          size="normal"
          variant="filled"
          value={username}
          onChange={(e) => setUsername(e.target.value)}
        />
        <div className="BoxValiderUsername">
          <Button variant="contained" onClick={() => ChangeUsername()}>
            Confirm
          </Button>
        </div>
      </div>
      <Button variant="contained" onClick={() => ChangeYourPassword()}>
        Reset your password
      </Button>
    </div>
  );
};

export default ProfilPage;
